.App {
    background: #3e4444;
}

#LOGO {
    color: white;
    font-size: 6em;
}

.App-header {
    text-align: center;
    background-color: #0f5f76;
    height: 150px;
    padding: 20px;
    color: white;
    border-radius: 0 0 20px 20px;
}

.App-title {
    font-size: 3em;
    margin: 5px;
}

h1{
    color: white;
}

p {
    word-wrap: break-word;
}

.input{
    width: 80%;
}

.thread, .threadqueue {
    word-wrap: break-word;
    width: 70%;
    min-height: 600px;
    display: block;
    margin: auto;
    background: #000000;
    padding: 10px;
}

.threadhead {
    background: #405d27;
    color: white;
    padding: 15px;
    margin: 5px;
    border-radius: 25px;
}
    .threadhead:hover {
        background: #5a3629;
    }
    .threadhead * {
        display: inline;
    }

.addthreadform {
    color: black;
}

.threadtop {
    background: #5a7b37;
    color: white;
    margin: 5px;
    border-radius: 25px;
    min-height: 200px;
    font-size: 1.5em;
}

.threadcontent {
    margin: 30px 30px;
    padding: 20px;
    white-space: pre-wrap;
}

.threadtitle {
    color: white;
    margin: 10px;
    text-decoration: underline;
}

.brownbox {
    background: #405d27;
    color: white;
    padding: 10px;
    margin: 5px;
    border-radius: 25px;
}

.textwrapper {
    display: inline;
    width: 50%;
}

.title {
    width: 80%;
    color: white;
    font-size: 3em;
    margin: 30px 10px;
}

.id{
    display: inline;
    width: 20%;
}

.backbutton{
    margin: 10px;
}


.postbutton {
    float: left;
    display: inline;
    margin: 15px;
    display: table-cell;
    font-size: 2em;
    margin: 10px;
}

.bigbutton {
    font-size: 2em;
    margin: 10px;
}


.button{
    margin: 5px;
}

.editbutton {
    float: right;
    color: #46dffa;
    cursor: pointer;
}

.editbutton:hover{
    text-decoration: underline;
}

.postinput{
    display: inline;
    margin: 20px;
    width: 75%;
}

.postedby {
    color: white;
}

.input {
    margin:10px;
}



